import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import NavigationBar from '../components/NavigationBar';
import styles from './MyPage.module.css';

const categories = [' ', ' ', '', ' '];

const MyPage: React.FC = () => {
  const navigate = useNavigate();
  const [selectedCategory, setSelectedCategory] = useState(' ');
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  useEffect(() => {
    //        
    const checkLoginStatus = () => {
      const isLoggedIn = localStorage.getItem('isLoggedIn') === 'true';
      if (!isLoggedIn) {
        console.log(' .');
        navigate('/login');
        return;
      }
      setIsLoggedIn(true);
    };

    checkLoginStatus();
  }, [navigate]);

  //      
  if (!isLoggedIn) {
    return (
      <div className={styles.loadingContainer}>
        <div className={styles.loadingSpinner}></div>
        <p>  ...</p>
      </div>
    );
  }

  const renderContent = () => {
    switch (selectedCategory) {
      case ' ':
        return (
          <div className={styles.uploadSection}>
            <h2> </h2>
            <div className={styles.uploadBox}>
              <div className={styles.uploadArea}>
                <div className={styles.uploadIcon}></div>
                <p>   </p>
                <button className={styles.uploadButton}> </button>
              </div>
            </div>
          </div>
        );
      case ' ':
        return (
          <div className={styles.savedPlacesSection}>
            <h2> </h2>
            <div className={styles.placesGrid}>
              {/*    */}
              <div className={styles.placeCard}>
                <h3> 1</h3>
                <p>:  </p>
                <p>: </p>
              </div>
              <div className={styles.placeCard}>
                <h3> 2</h3>
                <p>:  </p>
                <p>: </p>
              </div>
            </div>
          </div>
        );
      case '':
        return (
          <div className={styles.analysisSection}>
            <h2></h2>
            <div className={styles.analysisGrid}>
              {/*     */}
              <div className={styles.analysisCard}>
                <h3> </h3>
                <div className={styles.chartPlaceholder}> </div>
              </div>
              <div className={styles.analysisCard}>
                <h3> </h3>
                <div className={styles.chartPlaceholder}> </div>
              </div>
            </div>
          </div>
        );
      case ' ':
        return (
          <div className={styles.savedPostsSection}>
            <h2> </h2>
            <div className={styles.postsList}>
              {/*    */}
              <div className={styles.postCard}>
                <h3>  1</h3>
                <p>: </p>
                <p>: 2024-03-20</p>
              </div>
              <div className={styles.postCard}>
                <h3>  2</h3>
                <p>: </p>
                <p>: 2024-03-19</p>
              </div>
            </div>
          </div>
        );
      default:
        return null;
    }
  };

  const handleLogout = () => {
    //   
    console.log('');
  };

  return (
    <div className={styles.mypageRoot}>
      <div className={styles.topBar}>
        <div className={styles.logo}>PointWise</div>
        <div className={styles.topRight}>
        </div>
      </div>
      <NavigationBar currentPage="mypage" />
      <div className={styles.content}>
        <div className={styles.mypageMain}>
          {/*   */}
          <aside className={styles.categoryPanel}>
            <div className={styles.categoryTitle}></div>
            {categories.map(cat => (
              <div 
                key={cat} 
                className={`${styles.categoryBox} ${selectedCategory === cat ? styles.active : ''}`}
                onClick={() => setSelectedCategory(cat)}
              >
                {cat}
              </div>
            ))}
          </aside>
          {/*   */}
          <section className={styles.mainSection}>
            {renderContent()}
          </section>
        </div>
      </div>
    </div>
  );
};

export default MyPage; 