import React, { useState } from 'react';
import NavigationBar from '../components/NavigationBar';
import MapVisualization from '../components/MapVisualization';
import { Box, TextField, Select, MenuItem, FormControl, InputLabel, Slider, Typography } from '@mui/material';
import styles from './MapPage.module.css';
import { useNavigate } from 'react-router-dom';

interface MarkerData {
  id: number;
  position: [number, number];
  name: string;
  population: number;
  competitors: number;
  revenue: number;
  score: number;
}

const MapPage: React.FC = () => {
  const [location, setLocation] = useState('');
  const [businessType, setBusinessType] = useState('');
  const [difficulty, setDifficulty] = useState<number>(50);
  const [selectedMarker, setSelectedMarker] = useState<MarkerData | null>(null);
  const navigate = useNavigate();

  const businessTypes = [
    '',
    '',
    '',
    '',
    '',
    ''
  ];

  const handleLogout = () => {
    // Implement logout functionality
  };

  return (
    <div className={styles.mapPageRoot}>
      <div className={styles.topBar}>
        <div className={styles.logo}>PointWise</div>
        <div className={styles.topRight}>
        </div>
      </div>
      <NavigationBar currentPage="map" />
      <div className={styles.content}>
        <div className={styles.leftPanel}>
          <div className={styles.section}>
            <Typography variant="h6" className={styles.sectionTitle}> </Typography>
            <TextField
              fullWidth
              variant="outlined"
              placeholder=" "
              value={location}
              onChange={(e) => setLocation(e.target.value)}
              className={styles.searchInput}
            />
          </div>

          <div className={styles.section}>
            <Typography variant="h6" className={styles.sectionTitle}> </Typography>
            <FormControl fullWidth className={styles.selectInput}>
              <InputLabel></InputLabel>
              <Select
                value={businessType}
                label=""
                onChange={(e) => setBusinessType(e.target.value)}
              >
                {businessTypes.map((type) => (
                  <MenuItem key={type} value={type}>{type}</MenuItem>
                ))}
              </Select>
            </FormControl>
          </div>

          <div className={styles.section}>
            <Typography variant="h6" className={styles.sectionTitle}> </Typography>
            <Slider
              value={difficulty}
              onChange={(_, value) => setDifficulty(value as number)}
              valueLabelDisplay="auto"
              min={0}
              max={100}
              className={styles.slider}
            />
            <div className={styles.levelLabels}>
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>

          {selectedMarker && (
            <div className={styles.analysisSection}>
              <Typography variant="h6" className={styles.sectionTitle}> </Typography>
              <div className={styles.analysisContent}>
                <Typography variant="subtitle1">{selectedMarker.name}</Typography>
                <Typography variant="body2"> : {selectedMarker.population}</Typography>
                <Typography variant="body2"> : {selectedMarker.competitors}</Typography>
                <Typography variant="body2"> : {selectedMarker.revenue}</Typography>
                <Typography variant="body2"> : {selectedMarker.score}</Typography>
              </div>
            </div>
          )}
        </div>

        <div className={styles.mapArea}>
          <MapVisualization />
        </div>
      </div>
    </div>
  );
};

export default MapPage; 